@import "utils";
@import "typography";
@import "blocks";

.main {
  &__content {
    display: grid;

    row-gap: 100px;
    padding-block: 100px;

    @include onTablet {
      row-gap: 130px;
      padding-block: 130px;
    }

    @include onDesktop {
      row-gap: 200px;
      padding-block: 200px;
    }
  }
}
